import React from 'react';
import { string, object, number, bool, func, node } from 'prop-types';
import { insertRule, deleteRule, newId, ruleMap, namespace, ssr, disableSsr } from './globals';

const
  propTypes = {
    effect: string,
    duration: number,
    delay: number,
    cascade: bool,    
    tag: string,
    className: string,
    style: object,
    animate: func,
    fraction: number,    
    onReveal: func,
    onAfterReveal: func,
    throttle: number,
    children: node.isRequired,
  },
  defaultProps = {
    duration: 1000,
    delay: 0,
    throttle: 66,
    fraction: 0.2,
    tag: 'div',
  };

class Reveal extends React.Component {

  constructor(props) {
    super(props);
    this.state = { legacyMode: false };
    this.scrollTimeout = void 0;
    this.showTimeout = void 0;
    this.forceReveal = false;
    this.id = newId();    
    ruleMap[this.id] = [];
    this.show = this.show.bind(this);
    this.reveal = this.reveal.bind(this);
    this.scrollThrottle = this.scrollThrottle.bind(this);
    this.resizeThrottle = this.resizeThrottle.bind(this);
    this.kick = this.kick.bind(this);
    this.saveRef = el => this.el = el;
  }

  static getTop({ offsetTop, offsetParent }) {
    return offsetTop + (offsetParent && Reveal.getTop(offsetParent));
  }

  inViewport() {
  	if (!this.el)
  		return false;  		  	 
  	const h = this.el.offsetHeight,
  				delta = window.pageYOffset - Reveal.getTop(this.el),
  				tail = Math.min(h,window.innerHeight)*this.props.fraction;  				
		return ( delta > tail - window.innerHeight) && (delta < h-tail);					 
  }

  newRule(rule, cascade = 0) {
  	ruleMap[this.id].push(insertRule(
  		`${this.props.tag}.${namespace}${this.id} ${cascade?`> *:nth-child(${cascade}) `:''}{${rule}}`));  		
  }
  
  delay() {
    return this.forceReveal 
           ? this.props.delay
           : this.props.delay>this.props.throttle
             ? this.props.delay - this.props.throttle
             : 0;
  }

  cascade(rule) {
    if (!this.props.cascade && !this.props.wave) {
      this.newRule(rule);
    	return this.delay();
    }
		const wave = this.props.cascade || this.props.wave;
		let waveDelay = typeof wave === 'boolean' ? this.props.duration/5 : wave, delaySum = this.delay()-waveDelay;      
      for (let i=1, max=React.Children.count(this.props.children)+1; i<max; i++, delaySum += waveDelay)       
        this.newRule(`${rule}animation-delay: ${delaySum}ms;`, i);      
    return delaySum;
  }

  hide() {
    this.cascade('visibility: hidden; opacity: 0;');
  }

  show() {
    if (!this) return;
    if ( !this.isShown && (this.forceReveal || this.inViewport()) ) {
      this.isShown = true;
      const delay = this.cascade('visibility: visible; opacity: 1;');
        if (this.props.animate)
          this.props.animate( () => false );
        if (this.props.onReveal)
          this.props.onReveal();
        if (this.props.onAfterReveal)
          window.setTimeout( this.props.onAfterReveal, this.props.duration + delay );
    }
    this.showTimeout = void 0;
  }

  reveal() {    
    if (!this) return;
    if ( this.forceReveal || this.inViewport() ) {
      this.clean(); 
      if(!this.props.animation)
        this.setState({ legacyMode: true });
      else {
        const rule = `
          visibility: visible;
          opacity: 1;
          animation-duration: ${this.props.duration}ms;
          animation-fill-mode: both;
          animation-name: ${this.props.animation()};
          animation-delay: ${this.delay()}ms;
        `;        
        const delay = this.cascade(rule);        
        if (this.props.onReveal)
          this.props.onReveal();
        if (this.props.onAfterReveal)
          window.setTimeout( this.props.onAfterReveal, this.props.duration + delay );        
      }      
    }   
    this.scrollTimeout = void 0;
  }

  scrollThrottle() {    
    // ignore reveal events as long as an reveal execution is in the queue
    if (!this) return;
    if (!this.scrollTimeout)
      this.scrollTimeout = window.setTimeout(this.reveal, this.props.throttle);    
  }

  resizeThrottle() {    
    // ignore reveal events as long as an reveal execution is in the queue
    if (!this) return;
    if (!this.resizeTimeout)
      this.resizeTimeout = window.setTimeout(this.show, this.props.throttle);    
  }

  clean() {
    //window.clearTimeout(this.scrollTimeout);
    //window.clearTimeout(this.resizeTimeout);
    window.removeEventListener('scroll', this.scrollThrottle);
    window.removeEventListener('orientationchange', this.scrollThrottle);
    window.removeEventListener('resize', this.resizeThrottle);
  }

  clearRules() {
    const total = ruleMap[this.id].length;
    for (let i=0; i<total; i++){
      let ruleId = ruleMap[this.id][i];
      deleteRule(ruleId);
      delete ruleMap[this.id][i];     
      for( const key in ruleMap )       
        if (ruleMap.hasOwnProperty(key)) 
          for (let j=0, len=ruleMap[key].length;j<len;j++)
             if(ruleMap[key][j] > ruleId)
              --ruleMap[key][j];                  
    }
    delete ruleMap[this.id];
  }

  componentWillUnmount() {
    this.clean();        
    this.clearRules();    
    ssr && disableSsr();
  }

  kick(forceReveal = false) {
    if (!this) return;
    this.forceReveal = forceReveal;
    if (!forceReveal) {
      window.addEventListener('scroll', this.scrollThrottle);
      window.addEventListener('orientationchange', this.scrollThrottle);
      window.addEventListener('resize', this.resizeThrottle); 
    }
    this.reveal();   
  }

  componentDidMount() {
  	if (!this.el)
  		return;
    const top = Reveal.getTop(this.el);
    if ( ssr && top < window.pageYOffset + window.innerHeight ) {
      this.forceReveal = true;
      this.show();
      return;
    }     
          
  	if (this.props.animate){  		
  		if (window.innerHeight > top) 
  			this.hide();  		
  		this.props.animate(this.kick);  			
  	}
  	else 
      this.kick(window.innerHeight > top);    
  }

  render() {  	
    const { id, tag: TagName, children, style, className } = this.props,
    	cls = `${ this.state.legacyMode ? this.props.effect : namespace + ' ' +namespace + this.id }${ className ? ' ' + className : '' }`;    
    return <TagName id={id} className={cls} style={style} ref={this.saveRef} children={children} />;
  }
  
}

Reveal.propTypes = propTypes;
Reveal.defaultProps = defaultProps;
export default Reveal;


